import React from 'react';
import LinkItem from './LinkItem.js';
import {Reveal} from 'react-genie';
import '../../CSS/Object/Projects/_scrollbar.css';

class FlexWrapper extends React.Component{
  render(){
    let items = [];

    const CONTENTS = [
      {id: "3drepresation", img_src: require('../../imgs/3DRepresation.PNG'), title: "3DRepresation"},
      {id: "gradientdetails", img_src: require('../../imgs/Gradient_Details.PNG'), title: "GradientDetails"},
      {id: "creativebox", img_src: require('../../imgs/ListMenue.PNG'), title: "CreativeBox"},
      {id: "textanimation", img_src: require('../../imgs/TextAnimation.PNG'), title: "TextAnimation"}
    ];

    CONTENTS.forEach((content, index) => { 
      items.push(
        <Reveal delay = {700}>
          <LinkItem id = {content.id}
                    img_src = {content.img_src}
                    title = {content.title}
                    className = {index !== (CONTENTS.length-1) ? 
                      "p-horizontal_item u-marginleft1" : "p-horizontal_item u-marginleft1 u-marginright1"}>

          </LinkItem>
        </Reveal>
      );
    });

    return(
      <div className = "f-background f-background_darkblue c-scrollsnap_item l-46spacing_wrapper">
        <h1 className = "c-text_white l-46spacing_conteiner4"><Reveal delay = {500}> My Work</Reveal> </h1>
        <ul className = " p-horizontal_list u-padding u-marginleft1 l-46spacing_conteiner6">

          {items}
        
        </ul>
        <div className = "p-progressbar"></div>
      </div>
    );
  }
}

export default FlexWrapper;
