import React from 'react';
import './GradientDetails.css';

class GradientDetails extends React.Component{
  render(){
    return(
      <details className = "details_wrapper" open>
        <summary  className="neon summary">summary</summary>
        <p className="neon item"><div className = "before"></div>content1</p>
        <p className="neon item">content2</p>
        <p className="neon item">content3</p>
        <p className="neon item">content4</p>
        <p className="neon item">content5</p>
      </details>
    );
  }
}

export default GradientDetails;
