import React from 'react';
import {Reveal} from 'react-genie';
import detailData from './DetailData.js';


class DetailWrapper extends React.Component{
  constructor(props){
    super(props);
    this.state = {
      title : "",
      content : "",
      img : [],
    };
  }

  componentDidMount(){
    let data = new detailData();
    this.setState(data.props[this.props.id]);
  }

  render(){
    return(
        <div className = "f-background f-background_winered c-text_white c-scrollsnap_item l-46spacing_wrapper u-marginbottom0">
            <div className = "l-46spacing_conteiner4 l-centering">
            {/* <Reveal delay = {500}> */}
              {this.state.img}
            {/* </Reveal> */}
            </div>
            <div className = "l-46spacing_conteiner6">
              <h2 className = "u-marginleft1">{this.state.title}</h2>
              <Reveal delay = {700}>
                <p className = "c-text_lineheight1 u-marginleft1">{this.state.content}</p>
              </Reveal>
              
            </div>
        </div>
        
    );
  }
}

export default DetailWrapper;