import React from 'react';
import ReactDOM from 'react-dom';
import { BrowserRouter, Route } from 'react-router-dom';
import Home from './JS/Home/Home.js';
import Templete from './JS/Templetes/TempleteWrapper.js';
import {RevealGlobalStyles} from 'react-genie';

/* CSSファイル読み込み */
import './CSS/Layout.css'
import './CSS/Foundation/_background.css'
import './CSS/Foundation/_normalize.css'
import './CSS/Foundation/_reset.css'
import './CSS/Object/Utility.css'
import './CSS/Object/Compornents/_text.css'
import './CSS/Object/Compornents/_img.css'
import './CSS/Object/Compornents/_scrollsnap.css'
import './CSS/Object/Compornents/_box.css'
import './CSS/Object/Projects/_chip.css'
import './CSS/Object/Projects/_holizontalscroll.css'
import './CSS/Object/Projects/_linkbox.css'
import './CSS/Object/Projects/_book.css'
import './CSS/Object/Projects/_codepen.css'


ReactDOM.render(<App />, document.getElementById('root'));

function App(){
  return(
    <BrowserRouter basename={process.env.PUBLIC_URL}>
      <RevealGlobalStyles />
      <Route exact path = '/' component = {Home} />
      <Route path = '/templete/:id' component = {Templete}  />
    </BrowserRouter>
  );
}
export default App;
