import React from 'react';

import DetailStructWrapper from './Details/DetailStructWrapper.js';
import DetailCodeWrapper from './Details/DetailCodeWrapper.js';
import ProgressBar from './Contents/3DProgressBar/3DProgressBar.js';
import GradientDetails from './Contents/GradientDetails/GradientDetails.js';
import CreativeBox from './Contents/CreativeBox/CreativeBox.js';
import ListItemEffects from './Contents/ListItemEffects.js';
import ChipsAnimation from './Contents/ChipsAnimation/ChipsAnimation.js';
import Book from './Contents/Book.js'

class TempleteWrapper extends React.Component{
  render(){
    return(
      <div className = "c-scrollsnap_wrapper ">
        <div className = "u-relative">
          <div className = "f-background f-background_purple c-scrollsnap_item l-centering u-flex_colum">
            <GettingContent id = {this.props.match.params.id} />
        </div>
        </div>
        <DetailStructWrapper id = {this.props.match.params.id+"Struct"} />
        <DetailCodeWrapper id = {this.props.match.params.id+"Code"} />
      </div>
      
    );
  }
}

 function GettingContent(props){
   if(props.id === "3drepresation"){return <ProgressBar />;}
   else if(props.id === "gradientdetails"){return <GradientDetails />;}
   else if(props.id === "creativebox"){return <CreativeBox />;}
   else if(props.id === "listitemeffects"){return <ListItemEffects />;}
   else if(props.id === "textanimation"){return <ChipsAnimation text="Text"
                                                                text_size="5vw"
                                                                wrapper_width="20vw"
                                                                wrapper_height="5vh"
                                                                chip_count={50}
                                                                chip_width={5}
                                                                chip_height={5} />;}
   else if(props.id === "book"){return <Book />;}
   else {return <div></div>}
 }

export default TempleteWrapper;
