import React from 'react';
import ChipsAnimation from '../Templetes/Contents/ChipsAnimation/ChipsAnimation.js';

/* タイトル部分 */
class Header extends React.Component{
  render(){
    return(
      
      <div className = "f-background f-background_winered c-scrollsnap_item  l-centering">
        <ChipsAnimation text="MyPortfolio"
                        wrapper_width="60vw"
                        wrapper_height="5em"
                        chip_count={150}
                        chip_width={5}
                        chip_height={5} />
      </div>
      
    );
  }
}

export default Header;
