import React from 'react';
import '../../../Contents/3DProgressBar/3DProgressBar.css';
import './3DProgressBarEx.css'

class ProgressBarEx extends React.Component{
  render(){
    return(
        <div className="progress-wrapper">
          <div className="progress-bar">
            <div className="side front">
              <p className = "c-text_size3rem c-text_centering fadeout">mouseover</p>
              <div className="bar"></div>
            </div>
            <div className="side back"><div className="bar"></div></div>
            <div className="side topex"><div className="barflow"></div></div>
            <div className="side bottom"><div className="bar"></div></div>
            <div className="side left"></div>
          </div>
        </div>
    );
  }
}

export default ProgressBarEx;