import React from 'react';

class ChipsEx extends React.Component{
  constructor(props){
    super(props);
    this.state = {
      styles : []
    }
  }

  componentDidMount() {
    this.transformID = setInterval(
      () => this.transform(),
      100
    );
  }

  componentWillUnmount() {
    clearInterval(this.transformID);
  }

  //乱数生成
   getRandom(min, max) {
        return Math.random() * (max - min) + min;
  }

  //スタイル変形
  transform(){
    const temp = [];
    for (var i = 0; i < this.props.chip_count; i++) {
      temp.push({
         top : this.getRandom(0, 100) + "%",
         left : this.getRandom(0, 100) + "%",
         width : this.getRandom(0, this.props.chip_width) + "rem",
         height : this.getRandom(0, this.props.chip_height) + "rem",
         transform : "translate(-50%, -50%)" + "rotate(" + this.getRandom(0, 360) + "deg)"
     });
    }
    this.setState({styles : temp});
  }

  render(){
    const chips = [];
    for(let i = 0; i < this.props.chip_count; i++){
      chips.push(<div className = {"p-chip_color"+i % 4 + " l-abusolute_z1"} style={this.state.styles[i]}></div>);
    }
    return(chips);
  }
}

export default ChipsEx;
