import React from 'react';
import Chips from './Chips.js';
import {Reveal} from 'react-genie';

class ChipAnimation extends React.Component{
  constructor(props) {
    super(props);
  }

  render(){
    /* スタイル指定 */
    const generateWrapperStyle = (_width, _height) => {
      return{
        width: _width,
        height: _height
      }
    };

    return(
      <div className="p-chip_base l-centering" id="base" style={generateWrapperStyle(this.props.wrapper_width, this.props.wrapper_height)}>
        {/* チップアニメーションテキスト */}
        <h1 className="c-text_white p-chip_text"><Reveal delay = {500}>{this.props.text}</Reveal></h1>
        {/* チップアニメーション背景 */}
        <Chips chip_count={this.props.chip_count}
               chip_width={this.props.chip_width}
               chip_height={this.props.chip_height} />
      </div>
    );
  }
}

export default ChipAnimation;
